<template>
  <div class="rainfall">
    <!-- 標題 -->
    <div class="row" style="margin: 0 15px 0 15px">
      <div class="col-12 col-xl-12 text-center">
        <span
          class="badge badge-pill badge-info animate__animated animate__fadeInUp title"
          style="animation-duration: 1s; animation-iteration-count: 1"
          >高雄地區降雨</span
        >
      </div>
    </div>
    <el-row v-loading="loading" element-loading-text="等待政府資料中...">
      <el-col :xs="24" :span="10">
        <!-- 地圖 -->
        <l-map class="map" :zoom="zoom" :options="options" :center="center">
          <!-- 載入地圖資料 -->
          <l-tile-layer :url="url" :attribution="attribution" />
          <!-- 地標 -->
          <l-marker
            ref="location"
            :lat-lng="item.local"
            v-for="item in originalData"
            :key="item.name"
            @click="tableHighlight(item.SiteName)"
          >
            <l-icon :icon-url="iconimg" />
            <!-- 點擊圖標會彈出顯示的文字 -->
            <l-popup style="text-align: center">
              <div>{{ item.SiteName }}</div>
            </l-popup>
          </l-marker>
        </l-map>
      </el-col>
      <!-- 表格 -->
      <el-col :xs="24" :span="14">
        <el-table :data="rainfallData" height="500" border style="width: 100%">
          <el-table-column
            sortable
            prop="Township"
            label="地區"
          ></el-table-column>
          <el-table-column
            sortable
            prop="SiteName"
            label="測站名稱"
          ></el-table-column>
          <el-table-column
            sortable
            prop="Rainfall10min"
            label="10分鐘累積雨量"
          ></el-table-column>
          <el-table-column
            sortable
            prop="Rainfall1hr"
            label="1小時累積雨量"
          ></el-table-column>
          <el-table-column
            sortable
            prop="Rainfall3hr"
            label="3小時累積雨量"
          ></el-table-column>
          <el-table-column
            sortable
            prop="Rainfall6hr"
            label="6小時累積雨量"
          ></el-table-column>
          <el-table-column
            sortable
            prop="Rainfall12hr"
            label="12小時累積雨量"
          ></el-table-column>
          <el-table-column
            sortable
            prop="Rainfall24hr"
            label="24小時累積雨量"
          ></el-table-column>
          <el-table-column
            sortable
            prop="NOW"
            label="今日累積雨量"
          ></el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <div class="remindText" v-if="rainfallData.length !== 0">
      <span>更新時間 : {{ rainfallData[0].updateTime }}</span>
      <span style="margin-left: 30px">單位 : 毫米</span>
    </div>
  </div>
</template>
<script>
import axios from "axios";

export default {
  mounted() {
    this.getMap();
    this.getData = setInterval(this.getMap, 180000);
  },
  destroyed() {
    clearInterval(this.getData);
  },
  data() {
    return {
      getData: "",
      loading: true,
      // ---------------------地圖資料---------------------
      //地圖縮放比例
      zoom: 8,
      //地圖中心
      center: [23.013986, 120.586665],
      // 地標圖示
      iconimg: require("@/assets/img/rainfallicon.png"),
      // iconimg: require("leaflet/dist/images/marker-icon.png"),
      //地圖使用網址
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      //地圖來源聲明
      attribution: `© <a href="http://osm.org/copyright">OpenStreetMap</a> contributors`,
      // 取消顯示地圖左上角縮放地圖的按鈕
      options: {
        zoomControl: false,
      },
      // ---------------------地圖打點資料---------------------
      rainfallData: [],
      originalData: [],
    };
  },
  methods: {
    async getMap() {
      // 取得降雨資料
      await axios
        .post(`${this.allURL}getRainfall`)
        .then((res) => {
          console.log(res);
          this.originalData = res.data.data;
          this.rainfallData = this.originalData;
          console.log(this.originalData);
          this.loading = false;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 點選地圖上的地標
    tableHighlight(val) {
      console.log(val);
      // 將資料的第一筆更改為點擊的那個位置的資料
      let data = this.originalData.filter((item) => {
        return item.SiteName === val;
      });
      this.rainfallData = this.originalData.filter((item) => {
        return item.SiteName !== val;
      });
      this.rainfallData.splice(0, 0, ...data);
      // 更改表格第一列的資料底色
      let highlightRow = document.getElementsByClassName("el-table__row")[0];
      highlightRow.style.backgroundColor = "#AAFFEE";
    },
  },
};
</script>
<style scoped lang="scss">
.rainfall {
  overflow: hidden;
  padding: 2rem 4rem;
  background-color: #e3f0f7;
  font-family: 微軟正黑體;
  font-size: 22px;
  .title {
    padding: 1%;
    letter-spacing: 15px;
    line-height: 60px;
    font-size: 2.5em;
    margin: 0 auto 10px auto;
    border-color: #dcdcdc;
    border-width: 2px;
    text-decoration: none;
    font-weight: 600;
    outline: 0;
  }
  .map {
    height: 500px;
    width: 90%;
    margin: 0 auto;
    font-size: 22px;
  }
  .remindText {
    text-align: right;
    font-weight: bold;
    margin: 10px 20px 0 0;
  }
  /deep/.el-table .cell {
    text-align: center;
    font-size: 22px;
  }
  /deep/.el-table th > .cell {
    font-size: 18px;
  }
  @media (max-width: 1280px) {
    /deep/.el-col-10 {
      width: 100%;
    }
    /deep/.el-col-14 {
      margin-top: 20px;
      width: 100%;
    }
  }
  @media (max-width: 450px) {
    padding: 2% 4%;
    font-size: 14px;
    .title {
      padding: 0px 15px;
      letter-spacing: 5px;
      font-size: 26px;
      line-height: 50px;
    }
    .map {
      height: 300px;
      width: 100%;
      font-size: 14px;
    }
    /deep/.cell {
      font-size: 8px;
      box-sizing: border-box;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: normal;
      word-break: break-all;
    }
    /deep/.el-table th > .cell {
      display: inline-block;
      box-sizing: border-box;
      position: relative;
      vertical-align: middle;
      width: 100%;
      padding-right: 0;
    }
    /deep/.el-table .cell {
      font-size: 12px;
    }
  }
}
</style>